.card{
    width: 330px;
    height: 430px;
    border-radius: 8px;
    box-shadow:  0 2px 2px black ;
    overflow: hidden;
    margin: 20px;
    text-align: center;
    transition: all 0.25s;
    font-family: 'Lucida Sans', sans-serif;

}

.conatiner .card:hover{
    transform: translateY(-15px);
    box-shadow: 0 12px 16px black ;
    font-family: 'Lucida Sans', sans-serif;
}

.container .card img{
    width: 200px;
    height: 150px;
}

.card{
    float: left;
    justify-content: space-between;
    display: flex;
    width: 350px;
    margin: 20px;
    font-family: 'Lucida Sans',sans-serif;
    position: relative;
    left: 85px;
}

.serachbar{
    width: 100%;
    padding: 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px;
    font-family: 'Lucida Sans', sans-serif;
}

.btnAdmin{
    padding: 5px 20px;
    border: none;
    font-family: 'Lucida Sans', sans-serif;
}

.btnAdmin:hover{
    background-color: aqua;
    color: antiquewhite;
    transition: 05.s;
    font-family: 'Lucida Sans', sans-serif;
}

.Tolanding{
    align-items: center;
    justify-content: space-between;
    display: flex;
    font-family: 'Lucida Sans', sans-serif;
}

.CreateActivities{
    color: antiquewhite;
    font-family: 'Lucida Sans', sans-serif;
}

.container{

    box-sizing: border-box;
    justify-content: center;
    display: absolute;
    border-radius: 5px;
    text-align: center;
    position: relative;
}

.ToActivities{
    position: relative;
    left: 120px;
}

.paginado{
