.body{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
}

.card{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: space-between;
    overflow: hidden;
    text-align: center;
    font-family: 'Lucida Sans',sans-serif;
}

.DivPais{
    justify-content: space-between;
    position: relative;
    top: -110px
}

.Pais{
    width: 100px;
    height: 100px;
    background-color: azure;
    border: 0.2px solid blue;
    display: inline-block;
    margin-left: 10px;
    margin-right: 10px;
    justify-content: space-between;
    border-radius: 6px;
    font-family: 'Lucida Sans', sans-serif;
}

.btnAdmin{

    padding: 4px 10px;
    border: none;
    background: transparent;
    color: beige;
    border: 0.5px solid blue;
    font-family: 'Lucida Sans',sans-serif;
}

.btnAdmin:hover{
    background-color: blueviolet;
    color: bisque;
    transition: 0.5s;
    border: 0.2px solid burlywood;

}
