.Creactividad{

    height: 500px;
    width: 1230px;
    padding: 200px;
    border:5px solid yellow;
    margin: 60px;
    box-sizing: border-box;
    box-shadow: 0 4px 4px black;
    overflow: hidden;
    backdrop-filter: blur(4px);
}

.divActividades{
    display: block;
    margin: 10px;
}

.probe{
    justify-content: center;
    display:absolute;
    border-radius: 5px;
    text-align: center;
    position: relative;
    top: -130px;
}
.body{
    justify-content: center;
}
.Body{
    justify-content: center;
}

.boton{
    border-radius: 10px;
    width: 25px;
    height: 25px;
    padding: 4px 10px;
    border: none;
    color: blue;
    border: 0.5px solid green;
    align-items: center;
    text-align: center;
}
.boton:hover{
    background-color: coral;
    color: white;
    transition: 0,5s;
}
.actividad{
    background-color: cadetblue;
}
.Text{
    color: aliceblue;
}
.Titulo{
    color:azure;
    position: relative;
    top:-150px;
}
.botonvolver{
    position: relative;
    top:-150px;
}